import styles from './Presentation.module.css'
import ButtonA from '../Elements/ButtonA'
import ButtonB from '../Elements/ButtonB'

function Presentation (){
    return (
        <div className={styles.presentation} id='Presentation' >
            <h4><strong>Bem-vindo ao meu Portiflio</strong></h4>

            <h1>Ol, eu sou o Joo Victor!</h1>

            <p>
                Sou apaixonado pela tecnologia e suas infinitas possibilidades. <br></br>
                Como estudante de programao fullstack, estou em constante aprimoramento <br></br> 
                para me tornar um programador excepcional. J contribu em diversos projetos <br></br>
                com solues inovadoras que impactaram positivamente a vida das pessoas. <br></br>
                Estou animado para continuar explorando o universo da tecnologia e inspirar <br></br>
                outros entusiastas a perseguirem seus sonhos.
            </p>

            <ButtonA link='https://github.com/srja0' text='Conecte-se comigo!'/>

        </div>
    )
}

export default Presentation